<template>
  <div class="file-list-view-wrapper">
    <ul class="list">
      <li v-for="file in filterFileList" :key="file.id">
        <!-- 图标 -->
        <i class="file-icon">
          <icon-font-box :type="file.icon" size="24" height="24" color="#fff" />
        </i>

        <!-- 文件名 -->
        <span class="file-name">{{ file.name }}</span>

        <!-- 文件大小 -->
        <span class="file-size">（{{ file.size }}）</span>

        <!-- 附件操作 -->
        <div class="handler" @click="handlerClick($event, file)">
          <a class="handler-item" href="javascript:void(0);" data-type="preview">预览</a>
          <a class="handler-item" href="javascript:void(0);" data-type="download">下载</a>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import { bytesFormat } from '@handday/utils'
import { IconFontBox } from '@handday/components'
export default {
  name: 'FileListView',
  components: {
    IconFontBox
  },
  props: {
    // 文件列表
    list: {
      required: true,
      type: Array,
      default: () => ([])
    }
  },
  computed: {
    // 过滤后的文件列表
    filterFileList () {
      return this.list.map(file => {
        return {
          ...file,
          size: bytesFormat(file.size),
          icon: 'icon' + this.getFileIconName(file.type || file.name)
        }
      })
    }
  },
  methods: {
    /**
     * 获取文件icon名称
     * @param {String} suffixName 后缀名
     */
    getFileIconName (suffixName) {
      // 文件类型组
      const iconTypeGroup = {
        image: ['png', 'jpg', 'jpeg', 'gif'],
        pdf: 'pdf',
        xls: ['xlsx', 'xls'],
        media: ['mp4'],
        audio: 'mp3',
        doc: ['doc', 'docx'],
        zip: ['zip', 'rar', '7z'],
        txt: 'txt',
        other: 'other'
      }
      try {
        suffixName = suffixName.slice(suffixName.lastIndexOf('.') + 1) // 后缀名
        for (const key in iconTypeGroup) {
          const itemTypes = iconTypeGroup[key]
          // 如果当前后缀在value中存在，key则为类型
          if (itemTypes.includes(suffixName)) {
            return key
          }
        }
      } catch (error) {
        console.log(error)
        return iconTypeGroup.other
      }
    },

    /***
     * 处理点击事件
     * @param {Event} e
     * @param {Object} file
     */
    handlerClick (e, file) {
      const event = e || window.event
      const handlerType = event.target.getAttribute('data-type')
      if (handlerType) {
        const handlerMap = {
          // 预览
          preview: () => {
            console.log('预览哦')
          },

          // 下载
          download: () => {
            console.log('下载哦')
          }
        }

        handlerMap[handlerType] && handlerMap[handlerType]() // 调用

        this.$emit('action', {
          event: handlerType,
          file
        })
      }
    }
  }
}
</script>

<style scoped lang="less">
.file-list-view-wrapper {
  user-select: none;
  .list {
    li {
      display: flex;
      align-items: center;
      .file-icon {
        width: 24px;
        height: 24px;
        margin-right: 8px;
        border-radius: 3px;
        // background: #558ECE;
      }

      .file-name,
      .file-size {
        line-height: 22px;
        font-size: 14px;
        font-weight: 400;
        color: #8c8c8c;
      }

      // 文件操作
      .handler {
        flex: 1;
        margin-left: 24px;
        .handler-item + .handler-item {
          margin-left: 16px;
        }
      }
    }

    li + li {
      margin-top: 12px;
    }
  }
}
</style>
